import React, { Component } from 'react';
import './App.css';
import Page1 from './components/page1/Page1';
import Page2 from './components/page2/Page2';
import Page3 from './components/page3/Page3';
import Page4 from './components/page4/Page4';
import Page5 from './components/page5/Page5';
import Page6 from './components/page6/Page6';
import Page7 from './components/page7/Page7';
import Page8 from './components/page8/Page8';
import music from './星星.mp3'

export default class App extends Component {

  constructor(props){
    super(props);
    this.state={
      page: 1
    }
  }

  componentDidMount(){
    let self = this;
    let music = document.getElementById('music');
    console.log(music)

    // setTimeout(()=>{
    //   music && music.play && music.play()
    // },3000)

    document.getElementById('home').addEventListener('touchstart', self.mouseDown)
    document.getElementById('home').addEventListener('touchmove', self.mouseMove)
    document.getElementById('home').addEventListener('touchend', self.mouseUp)

    setTimeout(()=>{
      self.setState({
        trans: true
      })
    },30)
  }

  getPoint = (e) => {
    return e.touches ? e.touches[0] : e;
  }

  mouseDown = (e) => {
    e.preventDefault();
    let point = this.getPoint(e);
    this.startY = point.pageY;
  }
  mouseMove = (e) => {
    e.preventDefault();
    let point = this.getPoint(e);
    this.endY = point.pageY;
  }

  mouseUp = (e) => {
    console.log(e, this.endY, this.startY)
    e.preventDefault();
    const { page } = this.state;
    if(this.endY > this.startY && this.endY - this.startY > 10){
      if(page == 1){
        return
      }
      this.setState({
        page: page - 1
      })
    }else if(this.startY > this.endY && this.startY - this.endY > 10) {
      if(page == 8){
        return
      }
      console.log('page: ', page)
      this.setState({
        page: page + 1
      })
    }
  }

  render(){
    const { page } = this.state;
    return (
      <div className="App" id="home">
        <audio
          id="music"
          loop="loop"
          autoplay="autoplay"
          // autoPlay={true}
          controls={false}
          // src="http://m10.music.126.net/20190712155126/48d0b21605e5ac4856afd0e3c06c8f1c/ymusic/b550/99d2/9354/4d6ef08a3f9ab13b87fb34079d51a5f8.mp3"></audio>
          src={music}
          // src="http://stor.cloudmusics.cn/mp3/2019/07/e3d93bbcb606acbb244f07dabb15649c.mp3"
          ></audio>
        {page == 1 && <Page1/>}
        {page == 3 && <Page2/>}
        {page == 4 && <Page3/>}
        {page == 5 && <Page4/>}
        {page == 6 && <Page5/>}
        {page == 7 && <Page6/>}
        {page == 8 && <Page7/>}
        {page == 2 && <Page8/>}
      </div>
    );
  }
}
